* {
  padding: 0;
  margin: 0;
  font-family: system-ui, system-ui, sans-serif;
}

.App {
  width: 100%;

}

.details {
  margin: auto;
  width: 100%;
}

table {
  width: 100%;


}

.styled-table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr {
  background-color: #dbf2f2;
  color: #000000;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 12px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #f2f8f8;
}

.styled-table tbody tr:hover {
  /* font-weight: bold; */
  color: #000000;
  background-color: #e9fafa;
}

.allord {
  color: #08bfbe;
  font-size: 1rem;
}

.Delivered {
  color: #0099CC;
  background-color: #33b6e529;
  padding: 6px;
  border-radius: 30%;
  box-sizing: border-box;
}

.Completed {
  color: #007E33;
  background-color: #00c85026;
  padding: 6px;
  border-radius: 30%;
  box-sizing: border-box;
}

.Prepared {
  color: #FF8800;
  background-color: #ffbb3324;
  padding: 6px;
  border-radius: 30%;
  box-sizing: border-box;
}

#form-input {
  display: block;
  margin-left: 2%;
  margin-right: auto;
  margin-top: 1%;
  width: 391px;
  border-radius: 6px;
  height: 40px;
  border: 1px solid #dddddd;
  outline: none;
  padding-left: 40px;
}

input:hover {
  box-shadow: 0px 1px 3px rgb(192, 185, 185);
}
.buttons {
  float:left;       
  padding: 10px 20px 10px 20px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.container {

  /* Center the button on the page */
  text-align: center;
  
  
}